"use strict";

var start_button = document.getElementById('start-button');
var reset_button = document.getElementById('reset-button');
var logo_img = document.getElementsByClassName('logo-img')[0];
var total = document.getElementById('total');
var total_text = document.getElementById('total-text');
var rules = document.getElementById('rules');
var game = document.getElementById('game');
var humanScore = 0;
var computerScore = 0;
var round = 0;
var img_choice = document.querySelectorAll('.human-choice-img');
var copmuter_choice_img = document.getElementsByClassName('computer-choice-img')[0];

var startGame = function startGame() {
  rules.style.display = 'none';
  game.style.display = 'flex';
  logo_img.style.display = 'none';
};

var resetGame = function resetGame() {
  rules.style.display = 'flex';
  logo_img.style.display = 'flex';
  game.style.display = 'none';
  copmuter_choice_img.src = 'img/question.jpg';
  humanScore = 0;
  computerScore = 0;
  round = 0;
  total.innerHTML = '';
  total_text.innerHTML = '';
};

start_button.addEventListener("click", startGame);
reset_button.addEventListener("click", resetGame);